.cart__container {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  display: none;
}

.cart__container.active {
  display: block;

}

.cart__container__bg {
  position: fixed;

  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.64);
  z-index: 999999;
}


.cart {
  position: fixed;
  top: 0;
  right: 0;
  width: 400px;
  height: 100%;
  background: #fff;
  z-index: 999999;
}

.cart__lose {
  width: 100%;
  height: 50px;
  padding: 10px 20px;

}

.cart__lose span i {
  font-size: 1.1rem;
  background: #212245;
  padding: 5px;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  z-index: 100000;
}

.cart__item-list {
  height: calc(100vh - 150px);
  overflow-y: scroll;
}

.cart__button {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 10px 20px;
  width: 100%;
  height: 80px;
  background: #df2020;
}

.cart__button h6 {
  font-size: 1rem;
  color: #fff;
}

.cart__button h6 span {
  font-size: 1.3rem;
  font-weight: 600;
}

.cart__button button {
  border: none;
  padding: 4px 20px;
  border-radius: 5px;
  background: #fff;
  color: #212245;
}

.cart__button button a {
  color: #212245;
  font-weight: 600;
}